<template>
    <el-col>       
        <el-col class="margT20">
            <el-col :span="8">
                <el-col :span="4" class="lih34"></el-col>
                <el-date-picker v-model="dateTime" type="datetime" placeholder=""></el-date-picker>
            </el-col>
            <el-col :span="9" :offset="1" v-for="item in selectD" :key="item">
                <elselect :selectLabel="item.label" :selectData="item.data"></elselect>
            </el-col>
            <el-col :span="6">
                <el-col :span="6" class="lih34"></el-col>
                <el-col :span="16"><el-input placeholder=""></el-input></el-col>
            </el-col>
        </el-col>

        <el-col class="margT20 margB20">
            <el-button class="btnw" type="info">&nbsp;&nbsp;</el-button>
        </el-col>

        <el-col>
            <data-tables :data='tableData' class="pagination" :row-action-def='getRowActionsDef()'>
                <el-table-column v-for="item in cols" :prop="item.prop" :label="item.label" :key="item.label"></el-table-column>
            </data-tables>
        </el-col>

    </el-col>
</template>
<script>
import elselect from '../components/select';
export default {
  data() {
    return {
        dateTime: '',
        selectD: [
            {
                label: '', 
                data: [
                    {'label': '', value: ''},
                    {'label': '', value: ''},
                    {'label': '', value: ''},
                    {'label': '', value: ''},
                    {'label': '', value: ''},
                ]
            }
        ],
        cols: [
            {prop: 'sort', label: '' },
            {prop: 'date', label: '' },
            {prop: 'safeState', label: '' },
            {prop: 'unit', label: '' },
            {prop: 'name', label: '' },
            {prop: 'phone', label: '' }
        ],
        tableData:[],
    }
  },
  created() {
      this.axios('/corridor', this.tableData).then((res)=>{
          this.tableData = res.data.corridor
      })
  },
  components: {
      elselect
  },
  methods: {
      getRowActionsDef(){
        let self = this;
        return [{
            name: '',
            handler(row) {
                this.num = row.num;
                self.flagMsg = true;
            },
        }]
      },
      
  }
}
</script>
<style lang="scss">
.el-date-editor.el-input {
    width: auto;
}
</style>
 


