<template>
    <el-row>
        <el-col class="margB20">
            <!--<el-col class="margB20">
                <router-link to="/smart/first/chargeMng/chargesta">
                    <el-button type="warning" class="btnw"></el-button>
                </router-link>
                <el-button class="btnw floatR" disabled="disabled"></el-button>
                <el-col class="clear"></el-col>
            </el-col>-->
            
            <el-col class="margT20">
                <el-col :span="6">
                    <el-col :span="6" class="lih34"></el-col>
                    <el-col :span="14">
                        <el-input placeholder=""></el-input>
                    </el-col>
                </el-col>
                <el-col :span="6">
                    <el-col :span="5" class="lih34"></el-col>
                    <el-col :span="15">
                        <el-input placeholder=""></el-input>
                    </el-col>
                </el-col>
                <el-col v-for="item in selectD" :key='item' :span="6">
                    <select1 :selectLabel="item.label" :selectData="item.data"></select1>
                </el-col>
            </el-col>
            
        </el-col>
        <el-col class="margB20">
            <el-button class="btnw floatL" type="info">&nbsp;&nbsp;</el-button>
            <el-button class="btnw floatR" type="success" @click="openMsgBox()"></el-button>
            <el-col class="clear"></el-col>
        </el-col>

        <el-col>
            <data-tables :data='tableData' class="pagination" :has-action-col="false">
                <el-table-column v-for="item in cols" :prop="item.prop" :label="item.label" :key="item.label" :width="item.width"></el-table-column>
            </data-tables>
        </el-col>

        <el-col v-if="flagMsg">
            <transition name="el-fade-in-linear">
                <div tabindex="-1" class="el-message-box__wrapper">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title"></div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn" @click="flagMsg = !flagMsg">
                                <i class="el-message-box__close el-icon-close"></i>
                            </button>
                        </div>
                        
                        <div class="el-message-box__content">
                            <div class="el-message-box__status"></div>
                            <el-col :span="12" class="margB20" v-for="item in inputData" :key="item">
                                <el-col :span="8" class="lih34 tx-r" :prop="item.prop">{{item.label}} :&nbsp;</el-col>
                                <el-col :span="16">
                                    <el-input v-model="item.value"></el-input>
                                </el-col>
                            </el-col>                        
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="cancel()">
                                <span></span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary" @click="promise()">
                                <span></span>
                            </button>
                        </div>
                    </div>
                </div>
            </transition>
        </el-col>

    </el-row>
</template>
<script>
import select1 from '../components/select';

export default {
  data() {
    return {
        flagMsg: false,
        tableData: [],
        selectD:[
            {
                label: '',
                data: [
                   {label: '', value: ''},
                   {label: '', value: ''},
                   {label: '', value: ''},
                   {label: '', value: ''},
                   {label: '', value: ''},
                   {label: '', value: ''},
                ],
            },
            {
                label: '',
                data: [
                    {label: '', value: ''},
                    {label: '', value: ''},
                    {label: '', value: ''},
                    {label: '', value: ''},
                    {label: '', value: ''},
                ],
            }
        ],
        cols: [
            { prop: 'sort', label: '' },
            { prop: 'num', label: '' },
            { prop: 'class', label: '' },
            { prop: 'size', label: '' },
            { prop: 'style', label: '' },
            { prop: 'name', label: '' },
            { prop: 'phone', label: '' },
            { prop: 'date', label: '' }
        ],
        inputData:[
            { label: '', value: '', prop: 'sort' },
            { label: '', value: '', prop: 'num' },
            { label: '', value: '', prop: 'class' },
            { label: '', value: '', prop: 'size' },
            { label: '', value: '', prop: 'style' },
            { label: '', value: '', prop: 'name' },
            { label: '', value: '', prop: 'phone' },
            { label: '', value: '', prop: 'date' },
        ]
    }
  },
  mounted() {
    let Mock = require('mockjs');
    let obj;
    for (var i = 0; i < 34; i++) {
        obj = Mock.mock({
            'sort|1-100': 1,
            'num|1-100': 1,
            'class|1': ['', ''],
            'size|60-1000': 1,
            'style|1': ['', '', '', ''],
            'name|1': ['@cname'],
            'phone|1-1000000000': 1,
            'date|1': '@date', 
        })
        this.tableData.push(obj);       
    }
  },
  components: {
    select1,
  },
  methods: {
    openMsgBox() {
        this.flagMsg = !this.flagMsg;
    },
    cancel() {
        this.flagMsg = !this.flagMsg;
    },
    promise() {
        this.flagMsg = !this.flagMsg;
        let obj = {};
        this.inputData.forEach(function(val){
            obj[val.prop] = val.value;
            val.value = '';
        })
        this.tableData.unshift(obj)
    },
  }
}
</script>
<style lang="scss">
.width {
    width: 22%;
}
.el-message-box__wrapper {
    z-index: 2007;
    background: rgba(0,0,0,.4);
}
.el-message-box {
    width: 600px;
}
</style>



