<template>
    <el-col>
        <el-col class="margT20">
            <el-col :span="6">
                <el-col :span="6" class="lih34"></el-col>
                <el-col :span="12"><el-input placeholder=""></el-input></el-col>
            </el-col>
            <el-col :span="6">
                <el-col :span="6" class="lih34"></el-col>
                <el-col :span="12"><el-input placeholder=""></el-input></el-col>
            </el-col>
            <el-col :span="6" v-for="item in selectD" :key="item">
                <elselect :selectLabel="item.label" :selectData="item.data"></elselect>
            </el-col>
        </el-col>

        <el-col class="margT20 margB20">
            <el-button class="btnw floatL" type="info">&nbsp;&nbsp;</el-button>
            <el-button class="btnw floatR" type="success" @click="openMsgBox()"></el-button>
            <el-col class="clear"></el-col>
        </el-col>

        <el-col>
            <data-tables :data='tableData' class="pagination" :row-action-def='getRowActionsDef()'>
                <el-table-column v-for="item in cols" :prop="item.prop" :label="item.label" :key="item.label"></el-table-column>
            </data-tables>
        </el-col>

        <el-col v-if="flagMsg">
            <transition name="el-fade-in-linear">
                <div tabindex="-1" class="el-message-box__wrapper">
                    <div class="el-message-box">
                        <div class="el-message-box__header">
                            <div class="el-message-box__title"></div>
                            <button type="button" aria-label="Close" class="el-message-box__headerbtn" @click="flagMsg = !flagMsg">
                                <i class="el-message-box__close el-icon-close"></i>
                            </button>
                        </div>
                        
                        <div class="el-message-box__content">
                            <div class="el-message-box__status"></div>
                            <el-col :span="12" class="margB20" v-for="item in inputData" :key="item">
                                <el-col :span="8" class="lih34 tx-r" :prop="item.prop">{{item.label}} :&nbsp;</el-col>
                                <el-col :span="16">
                                    <el-input v-model="item.value"></el-input>
                                </el-col>
                            </el-col>                        
                        </div>
                        <div class="el-message-box__btns">
                            <button type="button" class="el-button el-button--default" @click="cancel()">
                                <span></span>
                            </button>
                            <button type="button" class="el-button el-button--default el-button--primary" @click="promise()">
                                <span></span>
                            </button>
                        </div>
                    </div>
                </div>
            </transition>
        </el-col>


    </el-col>
</template>
<script>
import elselect from '../components/select';
export default {
  data() {
      return {
        flagMsg: false,
        tableData:[],
        selectD: [
            {
                label: '',
                data: [
                    { label: '', value: '' },
                    { label: '', value: '' },
                    { label: '', value: '' },
                    { label: '', value: '' }
                ],
            },
            {
                label: '',
                data: [
                    { label: '', value: '' },
                    { label: '', value: '' },
                    { label: '', value: '' },
                    { label: '', value: '' },
                ],
            }
        ],
        cols: [
            {prop: 'sort', label: '' },
            {prop: 'num', label: '' },
            {prop: 'tab', label: '' },
            { prop: 'class', label: '' },
            { prop: 'bname', label: '' },
            { prop: 'phone', label: '' },
            { prop: 'bdate', label: '' },
            { prop: 'cdate', label: '' }
        ],
        inputData: [
            {prop: 'sort', label: '', value: '' },
            {prop: 'num', label: '', value: '' },
            {prop: 'tab', label: '', value: '' },
            { prop: 'class', label: '', value: '' },
            { prop: 'bname', label: '', value: '' },
            { prop: 'phone', label: '', value: '' },
            { prop: 'bdate', label: '', value: '' },
            { prop: 'cdate', label: '', value: '' }
        ],
      }
  },
  created() {
      this.axios('/water', this.tableData).then((res)=>{
          this.tableData = res.data.water
      })
  },
  components: {
      elselect
  },
  methods: {
    getRowActionsDef() {
        let self = this;
        return [{
            name: '',
            handler(row) {
                // self.$message('Edit clicked');
                this.num = row.num;
                self.flagMsg = true;
            },
        }]
    },
    openMsgBox() {
        this.flagMsg = !this.flagMsg;
    },
    cancel() {
        this.flagMsg = !this.flagMsg;
    },
    promise() {
        this.flagMsg = !this.flagMsg;
        let obj = {};
        this.inputData.forEach(function(val){
            obj[val.prop] = val.value;
            val.value = '';
        })
        this.tableData.unshift(obj)
    },
  }
}
</script>
<style lang="scss">
.el-message-box__wrapper {
    z-index: 2007;
    background: rgba(0,0,0,.4);
}
.el-message-box {
    width: 700px;
}
</style>


