import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import smart from '@/pages/smart'
import first from '@/pages/first'
import houseMng from '@/pages/houseMng'
import chargeMng from '@/pages/chargeMng'
import chargesta from '@/pages/chargestatistics';
import carMng from '@/pages/carMng';
import emptyCar from '@/pages/emptyCar';
import leaseMng from '@/pages/leaseMng';
import leaseContract from '@/pages/leaseContract';
import householdMng from '@/pages/householdMng';
import serviceMng from '@/pages/serviceMng';
import securityMng from '@/pages/securityMng';
import layout from '@/pages/layout';
import closestool from '@/pages/closestool';
import lamp from '@/pages/lamp';
import waterway from '@/pages/waterway';
import green from '@/pages/green';
import road from '@/pages/road';
import entranceGuard from '@/pages/entranceGuard';
import lift from '@/pages/lift';
import fire from '@/pages/fire';
import fireState from '@/pages/fireState';
import healthMng from '@/pages/healthMng';
import corridor from '@/pages/corridor';
import park from '@/pages/park';
import settings from '@/pages/settings';
import householdStatistics from '@/pages/householdStatistics';
import advert from '@/pages/advert';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', name: '', component: login
    },
    {
      path: '/smart',
      name: '',
      component: smart,
      redirect: '/smart/first',
      children: [
        {
          path: 'first', 
          name: "", 
          component: layout, 
          redirect: '/smart/first/index',
          children: [
            { path: 'index', name: '', component: first },
            { path: 'houseMng', name: "", component: houseMng },
            {
              path: 'leaseMng', 
              name: "", 
              component: layout, 
              redirect: '/smart/first/lease',
              children: [
                { path: 'lease', name: "", component: leaseMng },
                { path: 'leaseContract', name: "", component: leaseContract },
              ]
            },
            { path: 'householdMng', name: "", component: householdMng},
            {
              path: 'chargeMng', 
              name: '', 
              component: layout, 
              redirect: '/smart/first/charge',
              children: [
                { path: 'charge', name: "", component: chargeMng },
                { path: 'chargesta', name: "", component: chargesta },                
              ]
            },            
            {
              path: 'carMng', 
              name: "", 
              component: layout, 
              redirect: '/smart/first/cars',
              children: [
                { path: 'cars', name: "", component: carMng },
                { path: 'emptyCar', name: "", component: emptyCar }
              ]
            }
          ]
        },
        {
          path: 'serviceMng', 
          name: "", 
          component: layout,
          redirect: '/smart/serviceMng/service',
          children: [
            { path: 'service', name: "", component: serviceMng },
            { path: 'closestool', name: "", component: closestool },
            { path: 'lamp', name: "", component: lamp },
            { path: 'waterway', name: "", component: waterway },
            { path: 'green', name: "", component: green },
            { path: 'road', name: "", component: road },
          ]
        },
        {
          path: 'securityMng', 
          name: "", 
          component: layout,
          redirect: '/smart/securityMng/security',
          children: [
            {path: 'security', name: "", component: securityMng},
            {path: 'entranceGuard', name: "", component: entranceGuard},
            {path: 'lift', name: "", component: lift},
            {
              path: 'fire', 
              name: "", 
              component: layout,
              redirect: '/smart/securityMng/security/huo/fire',
              children: [
                { path: 'huo', name: '', component: fire },
                { path: 'fireState', name: '', component: fireState }                
              ]
            }
          ],
          
        },
        {
          path: 'healthMng',
          name: '',
          component: layout,
          redirect: '/smart/healthMng/health',
          children: [
            { path: 'health', name:'', component: healthMng },
            { path: 'corridor', name:'', component: corridor },
            { path: 'park', name:'', component: park },
          ]
        },
        {
          path: 'settings',
          name: '',
          component: layout,
          redirect: '/smart/settings/setting',
          children: [
            { path: 'setting', name: '', component: settings },
            { path: 'householdStatistics', name: '', component: householdStatistics },
            { path: 'advert', name: '', component: advert }
          ]
        }

      ]
    }
  ]
})




