<template>
  <el-col>
    <el-col class="margB20">
        <el-col class="margB20"></el-col>
        <el-col>
            <el-col v-for="item in selectD" :key='item.label' :span="7">
                <select1 :selectLabel="item.label" :selectData="item.data"></select1>            
            </el-col>
            <el-col :span="9">
                <el-col :span="4" class="lih34"> </el-col>
                <el-col :span="10">
                    <el-input placeholder=""></el-input>
                </el-col>
            </el-col>
        </el-col>                
    </el-col>
    <el-col class="margB20">
        <el-button class="btnw floatL" type="info">&nbsp;&nbsp;</el-button>
        <el-button class="btnw floatR" type="success" @click="openMsgBox()"></el-button>
        <el-col class="clear"></el-col>
    </el-col>
    
    <el-col>
        <data-tables :data='tableData' class="pagination" :row-action-def='getRowActionsDef()'>
            <el-table-column v-for="item in cols" :prop="item.prop" :label="item.label" :key="item.label"></el-table-column>            
        </data-tables>
    </el-col>
    
    <router-view></router-view>

    <el-col v-if="flagMsg">
        <transition name="el-fade-in-linear">
            <div tabindex="-1" class="el-message-box__wrapper">
                <div class="el-message-box">
                    <div class="el-message-box__header">
                        <div class="el-message-box__title"></div>
                        <button type="button" aria-label="Close" class="el-message-box__headerbtn" @click="flagMsg = !flagMsg">
                            <i class="el-message-box__close el-icon-close"></i>
                        </button>
                    </div>
                    
                    <div class="el-message-box__content">
                        <div class="el-message-box__status"></div>
                        <el-col :span="12" class="margB20" v-for="item in inputData" :key="item">
                            <el-col :span="8" class="lih34 tx-r" :prop="item.prop">{{item.label}} :&nbsp;</el-col>
                            <el-col :span="16">
                                <el-input v-model="item.value"></el-input>
                            </el-col>
                        </el-col>                        
                    </div>
                    <div class="el-message-box__btns">
                        <button type="button" class="el-button el-button--default" @click="cancel()">
                            <span></span>
                        </button>
                        <button type="button" class="el-button el-button--default el-button--primary" @click="promise()">
                            <span></span>
                        </button>
                    </div>
                </div>
            </div>
        </transition>
    </el-col>
  </el-col>
</template>
<script>
import select1 from '../components/select';
export default {
  data() {
    return {
        tableData: [],
        selectD:[
            {
                label: '',
                data: [
                    {label: '1', value: '1'},
                    {label: '2', value: '2'},
                    {label: '3', value: '3'},
                    {label: '4', value: '4'},
                    {label: '5', value: '5'}
                ],
            },
            {
                label: '',
                data: [
                    {label: '1', value: '1'},
                    {label: '2', value: '2'},
                    {label: '3', value: '3'},
                    {label: '4', value: '4'},
                    {label: '5', value: '5'},
                    {label: '6', value: '6'},
                    {label: '7', value: '7'},
                    {label: '8', value: '8'}
                ],
            }
        ],
        cols: [
            { prop: 'num', label: '' },
            { prop: 'fname', label: '' },
            { prop: 'fphone', label: '' },
            { prop: 'zname', label: '' },
            { prop: 'zphone', label: '' },
            { prop: 'year', label: '' },
            { prop: 'start', label: '' },
            { prop: 'end', label: '' },
        ],
        inputData:[
            { label: '', value: '', prop: 'num' },
            { label: '', value: '', prop: 'fname' },
            { label: '', value: '', prop: 'fphone' },
            { label: '', value: '', prop: 'zname' },
            { label: '', value: '', prop: 'zphone' },
            { label: '', value: '', prop: 'year' },
            { label: '', value: '', prop: 'start' },
            { label: '', value: '', prop: 'end' },
        ],
        flagMsg: false,
    }
  },
  mounted() {
    this.axios.get('/leaseMng').then((res) => {
        this.tableData = res.data.leaseMng;
    }).catch((error) => {
        console.log(error);
    })
  },
  components: {
    select1,
  },
  methods: {
    getRowActionsDef(){
      let self=this;
      return [{
        handler(row) {
            self.flagMsg = !self.flagMsg; 
            self.num = row.num;        
        },
        name: ''
      },{
        handler(row) {
          self.$message(' clicked')
        },
        name: ''
      }]
    },
    openMsgBox() {
        this.flagMsg = !this.flagMsg;
    },
    cancel() {
        this.flagMsg = !this.flagMsg;
    },
    promise() {
        this.flagMsg = !this.flagMsg;
        let obj = {};
        this.inputData.forEach(function(val){
            obj[val.prop] = val.value;
            val.value = '';
        })
        this.tableData.unshift(obj)
    },
  }
}
</script>
<style lang="scss">
.el-message-box__wrapper {
    z-index: 2007;
    background: rgba(0,0,0,.4);
}
.el-message-box {
    width: 700px;
}
</style>



