<template>
    <el-col>
        <el-col class="margT20">
            <el-col :span="7">
                <el-col :span="5" class="lih34"></el-col>
                <el-date-picker v-model="dateTime" type="datetime" placeholder=""></el-date-picker>
            </el-col>
            <el-col :span="7" :offset="1" v-for="item in selectD" :key="item">
                <elselect :selectLabel="item.label" :selectData="item.data"></elselect>
            </el-col>
            <el-col :span="7">
                <el-col :span="5" class="lih34"></el-col>
                <el-col :span="15"><el-input placeholder=""></el-input></el-col>
            </el-col>
        </el-col>

        <el-col class="margT20 margB20">
            <el-button class="btnw floatL" type="info">&nbsp;&nbsp;</el-button>
        </el-col>

        <el-col>
            <data-tables :data='tableData' class="pagination" :has-action-col="false">
                <el-table-column v-for="item in cols" :prop="item.prop" :label="item.label" :key="item.label"></el-table-column>
            </data-tables>
        </el-col>

    </el-col>
</template>
<script>
import elselect from '../components/select';
export default {
  data() {
      return {
        dateTime: '',
        tableData: [],
        selectD:[{
            label: '',
            data: [
                {value: '', label: '' },
                {value: '', label: '' },
                {value: '', label: '' }
           ]
        }],
        cols: [
            {prop: 'num', label: '' },
            {prop: 'date', label: '' },
            {prop: 'houseNum', label: '' },
            {prop: 'class', label: '' },
            {prop: 'door', label: '' },
            {prop: 'flag', label: '' },
            {prop: 'floor', label: '' }            
        ]
      }
  },
  created() {
    this.axios('/door', this.tableData).then((res)=>{
        this.tableData = res.data.door
    })
  },
  components: {
      elselect
  },
  methods: {
      getRowActionsDef() {
        let self = this;
        return [{
            name: '',
            handler(row) {
                this.num = row.num;
                self.flagMsg = true;
            },
        }]
      }
  }
}
</script>
<style lang="scss">
.el-date-editor.el-input {
    width: auto;
}
</style>
 


